import React from 'react';
import { Card, Button, Container, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

const WelcomePage = () => {
  const { isAuthenticated } = useAuth();
  
  // Eer kullanc giri yapmsa Dashboard'a ynlendir
  if (isAuthenticated) {
    window.location.href = '/dashboard';
    return null;
  }

  return (
    <div className="welcome-page">
      {/* Hero Section */}
      <div className="hero-section text-center text-white py-5">
        <Container>
          <div className="hero-content">
            <h1 className="display-3 fw-bold mb-4">
               Gelir-Gider Takip Uygulamas
            </h1>
            <p className="lead mb-4">
              Finansal hedeflerinize ulamann en akll ve profesyonel yolu
            </p>
            <div className="hero-buttons">
              <Button 
                as={Link} 
                to="/register" 
                variant="success" 
                size="lg"
                className="me-3 px-4 py-2"
              >
                 cretsiz Bala
              </Button>
              <Button 
                as={Link} 
                to="/login" 
                variant="outline-light" 
                size="lg"
                className="px-4 py-2"
              >
                 Giri Yap
              </Button>
            </div>
          </div>
        </Container>
      </div>

      {/* Features Section */}
      <Container className="py-5">
        <Row className="text-center mb-5">
          <Col>
            <h2 className="display-5 fw-bold text-primary mb-3">
               Neden Bu Uygulamay Semelisiniz?
            </h2>
            <p className="lead text-muted">
              Profesyonel finansal ynetim iin gelitirilmi zellikler
            </p>
          </Col>
        </Row>

        <Row className="g-4 mb-5">
          <Col lg={4} md={6}>
            <Card className="h-100 border-0 shadow-sm feature-card">
              <Card.Body className="text-center p-4">
                <div className="feature-icon mb-3">
                  <span className="display-4"></span>
                </div>
                <h4 className="fw-bold mb-3">Detayl Finansal Analiz</h4>
                <p className="text-muted">
                  Gelir-gider trendlerinizi grsel grafiklerle analiz edin. 
                  Aylk, yllk karlatrmalar ve kategorik dalmlar ile 
                  finansal durumunuzu net bir ekilde grn.
                </p>
                <ul className="text-start text-muted">
                  <li> Trend analizi ve tahminler</li>
                  <li> Kategori baznda gider dalm</li>
                  <li> Net gelir hesaplamalar</li>
                </ul>
              </Card.Body>
            </Card>
          </Col>

          <Col lg={4} md={6}>
            <Card className="h-100 border-0 shadow-sm feature-card">
              <Card.Body className="text-center p-4">
                <div className="feature-icon mb-3">
                  <span className="display-4"></span>
                </div>
                <h4 className="fw-bold mb-3">oklu Hesap Ynetimi</h4>
                <p className="text-muted">
                  Tm banka hesaplarnz tek yerden takip edin. 
                  Mevduat, kredili hesaplar ve zel limitler ile 
                  tam kontrol salayn.
                </p>
                <ul className="text-start text-muted">
                  <li> IBAN ve hesap bilgileri</li>
                  <li> Vadeli/vadesiz hesap trleri</li>
                  <li> Kredi limiti takibi</li>
                </ul>
              </Card.Body>
            </Card>
          </Col>

          <Col lg={4} md={6}>
            <Card className="h-100 border-0 shadow-sm feature-card">
              <Card.Body className="text-center p-4">
                <div className="feature-icon mb-3">
                  <span className="display-4"></span>
                </div>
                <h4 className="fw-bold mb-3">Kredi Kart Takibi</h4>
                <p className="text-muted">
                  Kredi kart limitlerinizi ve deme tarihlerini kolayca ynetin. 
                  Harcama takibi ve deme planlamas ile borlarnz kontrol altnda tutun.
                </p>
                <ul className="text-start text-muted">
                  <li> Hesap kesim tarihi takibi</li>
                  <li> Kalan limit hesaplamas</li>
                  <li> deme hatrlatclar</li>
                </ul>
              </Card.Body>
            </Card>
          </Col>

          <Col lg={4} md={6}>
            <Card className="h-100 border-0 shadow-sm feature-card">
              <Card.Body className="text-center p-4">
                <div className="feature-icon mb-3">
                  <span className="display-4"></span>
                </div>
                <h4 className="fw-bold mb-3">Ev Giderleri Ynetimi</h4>
                <p className="text-muted">
                  Kira, aidat, vergi gibi ev giderlerini detayl kaydedin. 
                  Szleme bilgileri ve deme takibi ile ev masraflarnz organize edin.
                </p>
                <ul className="text-start text-muted">
                  <li> Kira ve aidat takibi</li>
                  <li> Szleme bilgileri</li>
                  <li> Gider kategorileri</li>
                </ul>
              </Card.Body>
            </Card>
          </Col>

          <Col lg={4} md={6}>
            <Card className="h-100 border-0 shadow-sm feature-card">
              <Card.Body className="text-center p-4">
                <div className="feature-icon mb-3">
                  <span className="display-4"></span>
                </div>
                <h4 className="fw-bold mb-3">Mobil Uyumlu Tasarm</h4>
                <p className="text-muted">
                  Her cihazdan kolayca eriim salayn. Responsive tasarm ile 
                  telefon, tablet ve bilgisayarnzdan rahata kullann.
                </p>
                <ul className="text-start text-muted">
                  <li> Mobil uyumlu arayz</li>
                  <li> Tm tarayclarda alr</li>
                  <li> Hzl ve gvenli</li>
                </ul>
              </Card.Body>
            </Card>
          </Col>

          <Col lg={4} md={6}>
            <Card className="h-100 border-0 shadow-sm feature-card">
              <Card.Body className="text-center p-4">
                <div className="feature-icon mb-3">
                  <span className="display-4"></span>
                </div>
                <h4 className="fw-bold mb-3">Gvenli Veri Ynetimi</h4>
                <p className="text-muted">
                  Verileriniz gvenle saklanr. JWT token tabanl authentication 
                  ve ifreli veri transferi ile bilgileriniz korunur.
                </p>
                <ul className="text-start text-muted">
                  <li> Gvenli giri sistemi</li>
                  <li> Veri ifreleme</li>
                  <li> Yedekleme ve senkronizasyon</li>
                </ul>
              </Card.Body>
            </Card>
          </Col>
        </Row>

        {/* Call to Action */}
        <Row className="text-center">
          <Col>
            <Card className="border-0 bg-gradient text-white">
              <Card.Body className="p-5">
                <h3 className="fw-bold mb-3"> Hemen Balayn!</h3>
                <p className="lead mb-4">
                  Finansal hedeflerinize ulamak iin bugn cretsiz hesap oluturun
                </p>
                <div className="d-flex justify-content-center gap-3 flex-wrap">
                  <Button 
                    as={Link} 
                    to="/register" 
                    variant="light" 
                    size="lg"
                    className="px-4 py-2 fw-bold"
                  >
                     cretsiz Kayt Ol
                  </Button>
                  <Button 
                    as={Link} 
                    to="/login" 
                    variant="outline-light" 
                    size="lg"
                    className="px-4 py-2"
                  >
                     Zaten Hesabm Var
                  </Button>
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default WelcomePage;
