import React, { useState, useEffect } from 'react';
import { Card, Row, Col, Button, Spinner, Alert, Badge } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import axios from 'axios';
import BackButton from './BackButton';
import './Dashboard.css';

const Dashboard = () => {
  const [dashboardData, setDashboardData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  // Gelir tr etiketlerini Trke'ye evir
  const getIncomeTypeLabel = (type) => {
    const types = {
      'salary': 'Maa',
      'part_time': 'Ek ',
      'rental': 'Kira Geliri',
      'investment': 'Yatrm',
      'food_card': 'Yemek Kart',
      'other': 'Dier'
    };
    return types[type] || type;
  };

  // deme yntemi etiketlerini Trke'ye evir
  const getPaymentMethodLabel = (method) => {
    const methods = {
      'cash': 'Nakit',
      'credit_card': 'Kredi Kart',
      'bank_transfer': 'Banka Transferi',
      'credit_account': 'Kredili Hesap'
    };
    return methods[method] || method;
  };

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      setLoading(true);
      const response = await axios.get('/api/dashboard');
      if (response.data.success) {
        setDashboardData(response.data.dashboard);
      }
    } catch (error) {
      setError('Dashboard verileri yklenirken hata olutu');
      console.error('Dashboard hatas:', error);
    } finally {
      setLoading(false);
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('tr-TR', {
      style: 'currency',
      currency: 'TRY'
    }).format(amount || 0);
  };

  if (loading) {
    return (
      <div className="container mt-4 text-center">
        <Spinner animation="border" role="status" variant="primary">
          <span className="visually-hidden">Ykleniyor...</span>
        </Spinner>
        <p className="mt-3">Dashboard verileri ykleniyor...</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container mt-4">
        <Alert variant="danger">
          <Alert.Heading>Hata!</Alert.Heading>
          <p>{error}</p>
          <Button variant="outline-danger" onClick={fetchDashboardData}>
             Tekrar Dene
          </Button>
        </Alert>
      </div>
    );
  }

  // Veri yoksa ho geldin mesaj gster
  if (!dashboardData || (dashboardData.totalIncome === 0 && dashboardData.totalExpense === 0)) {
    return (
      <div className="container mt-4">
        <div className="text-center mb-5">
          <h1 className="display-4 text-primary"> Ho Geldiniz!</h1>
          <p className="lead text-muted">
            Gelir-Gider Takip uygulamanza ho geldiniz. lk gelir ve giderlerinizi ekleyerek balayn.
          </p>
        </div>

        {/* Hzl Balang Kartlar */}
        <Row className="mb-4">
          <Col md={6}>
            <Card className="text-center border-success h-100 shadow">
              <Card.Body>
                <h2 className="text-success mb-3"></h2>
                <h4>lk Gelirinizi Ekleyin</h4>
                <p className="text-muted">
                  Maa, kira geliri veya dier gelirlerinizi kaydedin ve finansal durumunuzu takip etmeye balayn.
                </p>
                <Button 
                  as={Link} 
                  to="/incomes/new" 
                  variant="success" 
                  size="lg"
                  className="px-4"
                >
                   Gelir Ekle
                </Button>
              </Card.Body>
            </Card>
          </Col>
          <Col md={6}>
            <Card className="text-center border-danger h-100 shadow">
              <Card.Body>
                <h2 className="text-danger mb-3"></h2>
                <h4>lk Giderinizi Ekleyin</h4>
                <p className="text-muted">
                  Ev kiras, faturalar, market alverii gibi giderlerinizi kaydedin ve harcama alkanlklarnz analiz edin.
                </p>
                <Button 
                  as={Link} 
                  to="/expenses/new" 
                  variant="danger" 
                  size="lg"
                  className="px-4"
                >
                   Gider Ekle
                </Button>
              </Card.Body>
            </Card>
          </Col>
        </Row>

        {/* Hesap ve Kredi Kart Kurulumu */}
        <Row className="mb-4">
          <Col md={12}>
            <Card className="shadow">
              <Card.Header className="bg-info text-white">
                <h5 className="mb-0"> Hesap ve Kredi Kart Kurulumu</h5>
              </Card.Header>
              <Card.Body>
                <p className="text-muted mb-3">
                  Daha detayl takip iin banka hesaplarnz ve kredi kartlarnz ekleyin.
                </p>
                <Row>
                  <Col md={6}>
                    <Button 
                      as={Link} 
                      to="/accounts/new" 
                      variant="outline-info" 
                      size="lg"
                      className="w-100 mb-2"
                    >
                       Hesap Ekle
                    </Button>
                  </Col>
                  <Col md={6}>
                    <Button 
                      as={Link} 
                      to="/credit-cards/new" 
                      variant="outline-warning" 
                      size="lg"
                      className="w-100 mb-2"
                    >
                       Kredi Kart Ekle
                    </Button>
                  </Col>
                </Row>
              </Card.Body>
            </Card>
          </Col>
        </Row>

        {/* zellikler */}
        <Row className="mb-4">
          <Col md={12}>
            <Card className="shadow">
              <Card.Header className="bg-dark text-white">
                <h5 className="mb-0"> Uygulama zellikleri</h5>
              </Card.Header>
              <Card.Body>
                <Row>
                  <Col md={4} className="text-center mb-3">
                    <h4 className="text-primary"></h4>
                    <h6>Detayl Analiz</h6>
                    <small className="text-muted">
                      Gelir-gider trendlerinizi ve kategori baznda dalmlarnz grn
                    </small>
                  </Col>
                  <Col md={4} className="text-center mb-3">
                    <h4 className="text-success"></h4>
                    <h6>Limit Takibi</h6>
                    <small className="text-muted">
                      Kredi kart ve hesap limitlerinizi gerek zamanl takip edin
                    </small>
                  </Col>
                  <Col md={4} className="text-center mb-3">
                    <h4 className="text-warning"></h4>
                    <h6>Ev Kiras Detaylar</h6>
                    <small className="text-muted">
                      Kira, aidat, vergi ve sigorta masraflarn ayr ayr kaydedin
                    </small>
                  </Col>
                </Row>
              </Card.Body>
            </Card>
          </Col>
        </Row>

        {/* Yenile Butonu */}
        <div className="text-center mb-4">
          <Button 
            variant="outline-primary" 
            size="lg"
            onClick={fetchDashboardData}
            disabled={loading}
            className="me-3"
          >
            {loading ? 'Yenileniyor...' : ' Verileri Yenile'}
          </Button>

        </div>
        

      </div>
    );
  }

  // Dashboard verilerini gvenli bir ekilde al
  const {
    totalAccounts = 0,
    totalCreditCards = 0,
    totalBalance = 0,
    totalCreditLimit = 0,
    availableCreditLimit = 0,
    totalIncome = 0,
    totalExpense = 0,
    netIncome = 0,
    foodCardIncome = 0,
    recentIncomes = [],
    recentExpenses = [],
    recentAccounts = [],
    recentCreditCards = []
  } = dashboardData || {};

  return (
    <div className="container mt-4">
      <BackButton />
      <h1 className="mb-4"> Gelir-Gider Takip Dashboard</h1>

      {/* Hzl Eriim Butonlar */}
      <Row className="mb-4">
        <Col md={12}>
          <Card className="shadow">
            <Card.Header className="bg-primary text-white">
              <h5 className="mb-0"> Hzl Eriim</h5>
            </Card.Header>
            <Card.Body>
              <Row>
                <Col md={3} className="mb-2">
                  <Button 
                    as={Link} 
                    to="/incomes/new" 
                    variant="success" 
                    size="lg" 
                    className="w-100"
                  >
                     Gelir Ekle
                  </Button>
                </Col>
                <Col md={3} className="mb-2">
                  <Button 
                    as={Link} 
                    to="/expenses/new" 
                    variant="danger" 
                    size="lg" 
                    className="w-100"
                  >
                     Gider Ekle
                  </Button>
                </Col>
                <Col md={3} className="mb-2">
                  <Button 
                    as={Link} 
                    to="/accounts/new" 
                    variant="info" 
                    size="lg" 
                    className="w-100"
                  >
                     Hesap Ekle
                  </Button>
                </Col>
                <Col md={3} className="mb-2">
                  <Button 
                    as={Link} 
                    to="/credit-cards/new" 
                    variant="warning" 
                    size="lg" 
                    className="w-100"
                  >
                     Kredi Kart Ekle
                  </Button>
                </Col>
              </Row>
              

            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Finansal zet */}
      <Row className="mb-4">
        <Col md={3}>
          <Card className="text-center border-success h-100">
            <Card.Body>
              <h3 className="text-success"></h3>
              <h5>Toplam Gelir</h5>
              <h3 className="text-success">{formatCurrency(totalIncome)}</h3>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3}>
          <Card className="text-center border-danger h-100">
            <Card.Body>
              <h3 className="text-danger"></h3>
              <h5>Toplam Gider</h5>
              <h3 className="text-danger">{formatCurrency(totalExpense)}</h3>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3}>
          <Card className={`text-center h-100 ${netIncome >= 0 ? 'border-success' : 'border-danger'}`}>
            <Card.Body>
              <h3 className={netIncome >= 0 ? 'text-success' : 'text-danger'}>
                {netIncome >= 0 ? '' : ''}
              </h3>
              <h5>Net Gelir</h5>
              <h3 className={netIncome >= 0 ? 'text-success' : 'text-danger'}>
                {formatCurrency(netIncome)}
              </h3>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3}>
          <Card className="text-center border-warning h-100">
            <Card.Body>
              <h3 className="text-warning"></h3>
              <h5>Yemek Kart Geliri</h5>
              <h3 className="text-warning">{formatCurrency(foodCardIncome)}</h3>
              <small className="text-muted">Sadece yemek giderlerinde kullanlr</small>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Hesap ve Kredi Kart zeti */}
      <Row className="mb-4">
        <Col md={6}>
          <Card className="shadow h-100">
            <Card.Header className="bg-info text-white">
              <h5 className="mb-0"> Hesap zeti</h5>
            </Card.Header>
            <Card.Body>
              <div className="d-flex justify-content-between align-items-center mb-3">
                <span>Toplam Hesap:</span>
                <Badge bg="primary" className="fs-5">{totalAccounts}</Badge>
              </div>
              <div className="d-flex justify-content-between align-items-center mb-3">
                <span>Toplam Bakiye:</span>
                <span className="fw-bold text-success fs-5">{formatCurrency(totalBalance)}</span>
              </div>
              <div className="d-grid gap-2">
                <Button 
                  as={Link} 
                  to="/accounts" 
                  variant="outline-info" 
                  size="sm"
                >
                   Tm Hesaplar Grntle
                </Button>
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="shadow h-100">
            <Card.Header className="bg-warning text-dark">
              <h5 className="mb-0"> Kredi Kart zeti</h5>
            </Card.Header>
            <Card.Body>
              <div className="d-flex justify-content-between align-items-center mb-3">
                <span>Toplam Kart:</span>
                <Badge bg="warning" className="fs-5">{totalCreditCards}</Badge>
              </div>
              <div className="d-flex justify-content-between align-items-center mb-3">
                <span>Toplam Limit:</span>
                <span className="fw-bold text-warning fs-5">{formatCurrency(totalCreditLimit)}</span>
              </div>
              <div className="d-flex justify-content-between align-items-center mb-3">
                <span>Kullanlabilir:</span>
                <span className="fw-bold text-success fs-5">{formatCurrency(availableCreditLimit)}</span>
              </div>
              <div className="d-grid gap-2">
                <Button 
                  as={Link} 
                  to="/credit-cards" 
                  variant="outline-warning" 
                  size="sm"
                >
                   Tm Kartlar Grntle
                </Button>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Son lemler */}
      <Row className="mb-4">
        <Col md={6}>
          <Card className="shadow h-100">
            <Card.Header className="bg-success text-white">
              <h5 className="mb-0"> Son Gelirler</h5>
            </Card.Header>
            <Card.Body>
              {recentIncomes && recentIncomes.length > 0 ? (
                <div>
                  {recentIncomes.map((income) => (
                    <div key={income.id} className="d-flex justify-content-between align-items-center mb-2 p-2 border-bottom">
                      <div>
                        <div className="fw-bold">{income.title || 'Balksz Gelir'}</div>
                        <small className="text-muted">
                          {getIncomeTypeLabel(income.income_type || 'other')}  {income.source || 'Bilinmiyor'}
                        </small>
                      </div>
                      <div className="text-end">
                        <div className="fw-bold text-success">{formatCurrency(income.amount || 0)}</div>
                        <small className="text-muted">
                          {income.income_date ? new Date(income.income_date).toLocaleDateString('tr-TR') : 'Tarih Yok'}
                        </small>
                      </div>
                    </div>
                  ))}
                  <div className="text-center mt-3">
                    <Button 
                      as={Link} 
                      to="/incomes" 
                      variant="outline-success" 
                      size="sm"
                    >
                       Tm Gelirleri Grntle
                    </Button>
                  </div>
                </div>
              ) : (
                <div className="text-center text-muted py-3">
                  <p>Henz gelir kayd bulunmuyor</p>
                  <Button 
                    as={Link} 
                    to="/incomes/new" 
                    variant="success" 
                    size="sm"
                  >
                     lk Geliri Ekle
                  </Button>
                </div>
              )}
            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="shadow h-100">
            <Card.Header className="bg-danger text-white">
              <h5 className="mb-0"> Son Giderler</h5>
            </Card.Header>
            <Card.Body>
              {recentExpenses && recentExpenses.length > 0 ? (
                <div>
                  {recentExpenses.map((expense) => (
                    <div key={expense.id} className="d-flex justify-content-between align-items-center mb-2 p-2 border-bottom">
                      <div>
                        <div className="fw-bold">{expense.title || 'Balksz Gider'}</div>
                        <small className="text-muted">
                          {expense.category_name || 'Kategori Yok'}  {getPaymentMethodLabel(expense.payment_method || 'cash')}
                        </small>
                        {(expense.category_name || expense.category_color) && (
                          <Badge 
                            bg="secondary" 
                            className="ms-2"
                            style={{ backgroundColor: expense.category_color || '#6c757d' }}
                          >
                            {expense.category_name || 'Kategori'}
                          </Badge>
                        )}
                      </div>
                      <div className="text-end">
                        <div className="fw-bold text-danger">{formatCurrency(expense.amount || 0)}</div>
                        <small className="text-muted">
                          {expense.created_at ? new Date(expense.created_at).toLocaleDateString('tr-TR') : 'Tarih Yok'}
                        </small>
                      </div>
                    </div>
                  ))}
                  <div className="text-center mt-3">
                    <Button 
                      as={Link} 
                      to="/expenses" 
                      variant="outline-danger" 
                      size="sm"
                    >
                       Tm Giderleri Grntle
                    </Button>
                  </div>
                </div>
              ) : (
                <div className="text-center text-muted py-3">
                  <p>Henz gider kayd bulunmuyor</p>
                  <Button 
                    as={Link} 
                    to="/expenses/new" 
                    variant="danger" 
                    size="sm"
                  >
                     lk Gideri Ekle
                  </Button>
                </div>
              )}
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Son Hesap ve Kredi Kart lemleri */}
      <Row className="mb-4">
        <Col md={6}>
          <Card className="shadow h-100">
            <Card.Header className="bg-info text-white">
              <h5 className="mb-0"> Son Hesap lemleri</h5>
            </Card.Header>
            <Card.Body>
              {recentAccounts && recentAccounts.length > 0 ? (
                <div>
                  {recentAccounts.map((account) => (
                    <div key={account.id} className="d-flex justify-content-between align-items-center mb-2 p-2 border-bottom">
                      <div>
                        <div className="fw-bold">{account.account_name || 'simsiz Hesap'}</div>
                        <small className="text-muted">{account.bank_name || 'Banka Yok'}</small>
                      </div>
                      <div className="text-end">
                                                <div className="fw-bold text-info">{formatCurrency(account.current_balance || 0)}</div>
                        <small className="text-muted">
                          {account.created_at ? new Date(account.created_at).toLocaleDateString('tr-TR') : 'Tarih Yok'}
                        </small>
                      </div>
                    </div>
                  ))}
                  <div className="text-center mt-3">
                    <Button 
                      as={Link} 
                      to="/accounts" 
                      variant="outline-info" 
                      size="sm"
                    >
                       Tm Hesaplar Grntle
                    </Button>
                  </div>
                </div>
              ) : (
                <div className="text-center text-muted py-3">
                  <p>Henz hesap kayd bulunmuyor</p>
                  <Button 
                    as={Link} 
                    to="/accounts/new" 
                    variant="info" 
                    size="sm"
                  >
                     lk Hesab Ekle
                  </Button>
                </div>
              )}
            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="shadow h-100">
            <Card.Header className="bg-warning text-dark">
              <h5 className="mb-0"> Son Kredi Kart lemleri</h5>
            </Card.Header>
            <Card.Body>
              {recentCreditCards && recentCreditCards.length > 0 ? (
                <div>
                  {recentCreditCards.map((card) => (
                    <div key={card.id} className="d-flex justify-content-between align-items-center mb-2 p-2 border-bottom">
                      <div>
                        <div className="fw-bold">{card.card_name || 'simsiz Kart'}</div>
                        <small className="text-muted">{card.bank_name || 'Banka Yok'}</small>
                      </div>
                      <div className="text-end">
                                                <div className="fw-bold text-warning">{formatCurrency(card.remaining_limit || 0)}</div>
                        <small className="text-muted">
                          {card.created_at ? new Date(card.created_at).toLocaleDateString('tr-TR') : 'Tarih Yok'}
                        </small>
                      </div>
                    </div>
                  ))}
                  <div className="text-center mt-3">
                    <Button 
                      as={Link} 
                      to="/credit-cards" 
                      variant="outline-warning" 
                      size="sm"
                    >
                       Tm Kartlar Grntle
                    </Button>
                  </div>
                </div>
              ) : (
                <div className="text-center text-muted py-3">
                  <p>Henz kredi kart kayd bulunmuyor</p>
                  <Button 
                    as={Link} 
                    to="/credit-cards/new" 
                    variant="warning" 
                    size="sm"
                  >
                     lk Kart Ekle
                  </Button>
                </div>
              )}
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Detayl Analiz */}
      <Row className="mb-4">
        <Col md={12}>
          <Card className="shadow">
            <Card.Header className="bg-dark text-white">
              <h5 className="mb-0"> Detayl Analiz</h5>
            </Card.Header>
            <Card.Body>
              <p className="text-muted mb-3">
                Gelir-gider trendlerinizi, kategori baznda dalmlarnz ve kullanlabilir limitlerinizi 
                detayl olarak inceleyin.
              </p>
              <div className="d-grid gap-2 d-md-flex justify-content-md-center">
                <Button 
                  as={Link} 
                  to="/analytics" 
                  variant="dark" 
                  size="lg"
                  className="px-4"
                >
                   Detayl Analizi Grntle
                </Button>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Yenile Butonu */}
      <div className="text-center mb-4">
        <Button 
          variant="outline-primary" 
          size="lg"
          onClick={fetchDashboardData}
          disabled={loading}
        >
          {loading ? 'Yenileniyor...' : ' Verileri Yenile'}
        </Button>
      </div>
    </div>
  );
};

export default Dashboard;
